@charset "UTF-8"; @keyframes swiper-preloader-spin {
	0% {
		 transform: rotate(0deg)
	}

	to {
		 transform: rotate(360deg)
	}
}

@keyframes my-animation {
	0% {
		 opacity: 0
	}

	to {
		 opacity: 1
	}
}

@keyframes bgAnimation {
	0%,to {
		 top: -50%;
		 left: -50%
	}

	25% {
		 top: 50%;
		 left: 0
	}

	50% {
		 top: 0;
		 left: 50%
	}

	75% {
		 left: 0;
		 top: -30%
	}
}

@keyframes move {
	0%,to {
		 border-radius: 200px
	}

	25% {
		 border-radius: 100px 200px 100px 200px
	}

	50% {
		 border-radius: 200px 100px 200px 100px
	}

	75% {
		 border-radius: 50px 200px 50px 200px
	}
}

@keyframes itemMove {
	0% {
		 transform: translateY(0);
		 margin-bottom: 0
	}

	15%,50%,100% {
		 transform: translateY(-10px);
		 margin-bottom: 0
	}

	30% {
		 transform: translateY(-5px);
		 margin-bottom: 0
	}
}

@keyframes iconMove {
	0%,to {
		 transform: rotate(0deg)
	}

	75% {
		 transform: rotate(-20deg)
	}
}

